import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

@Component({
  template: `
    <div class="forbidden text-center">
        <div class="status">
            <p class="forbidden-heading">403</p>
            <p class="heading-line">Access Denied / Forbidden</p>
            <p class="small">{{pageMessage}}</p>
        </div>
    </div>
  `,
  styles: [`
    .forbidden{display:flex;justify-content:center;background-color:#fff;height:90vh}.forbidden .status{text-align:center;margin-top:15%}.forbidden .status p{font-family:OpenSans-Regular;margin:0;line-height:1.5em}.forbidden .status p.forbidden-heading{font-size:55px}.forbidden .status p.heading-line{font-size:30px}.forbidden .status p.small{font-size:14px}
  `],
  selector: 'forbidden'
})
export class ForbiddenComponent implements OnInit {

  @Output() onLogout = new EventEmitter<string>();
  @Input () pageMessage: string;

  user = ' ';
  private isChange = false;
  message: string;
  constructor() {

  }

  ngOnInit() {
    // tslint:disable-next-line:max-line-length
    const defaultMsg = 'The page or resource you were trying to reach is restricted, please request application administrator for valid permissions.';
    if (!this.pageMessage) {
      this.pageMessage = defaultMsg;
    }
  }
}
